import axios from "axios";
import { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import { useParams } from "react-router-dom"
import { Crypto } from "../Context/Context";
import "../Styles/Coinpage.css"

import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
  } from 'chart.js';

ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
)

const Coinpage = () => {

    const { id } = useParams()

    const { currency } = Crypto()

    const [coin, setCoin] = useState({
        name: "",
        image: "",
        description: "",
        rank: "",
        market: "",
        price: ""
    })

    const [chart, setChart] = useState([])

    const [ period, setPeriod ] = useState(10)

    useEffect(async () => {

        const { data } = await axios.get(`https://api.coingecko.com/api/v3/coins/${id}`)

        const histdata = await axios.get(`https://api.coingecko.com/api/v3/coins/${id}/market_chart?vs_currency=${currency}&days=${period}`)

        setChart(histdata.data.prices)

        setCoin({
            name: data.name,
            image: data.image.large,
            description: data.description.en.split(". ")[0],
            rank: data.market_cap_rank,
            market: data.market_data.market_cap[currency],
            price: data.market_data.current_price[currency]
        })

    }, [currency, period])

    return (
        <div className="Coinpage">

            <div className="Coininfo">
                <img src={coin.image} alt="he" />
                <h1>{coin.name}</h1>
                <p dangerouslySetInnerHTML={{ __html: coin.description }}></p>

                <div>
                    <h3>Rank - {coin.rank}</h3>

                    {
                        currency == 'usd' ? (
                            <>
                                <h3>Price - ${coin.price}</h3>
                                <h3>Market Cap - ${coin.market}</h3>
                            </>
                        ) : (
                            <>
                                <h3>Price - {coin.price}</h3>
                                <h3>Market Cap - {coin.market}</h3>
                            </>
                        )
                    }
                </div>
            </div>

            <div className="Chart">

                <div>
                    <button onClick={()=>setPeriod(10)}>10 Days</button>
                    <button onClick={()=>setPeriod(30)}>30 Days</button>
                    <button onClick={()=>setPeriod(365)}>365 Days</button>
                </div>

                

                <Line

                    data={{
                        labels: chart.map((coin) => {
                            let date = new Date(coin[0])
                            return date.toLocaleDateString()
                        }),
                        datasets: [
                            {
                                data: chart.map((coin) => { return coin[1] }),
                                label: `Price past ${period} days`,
                                borderColor: "tomato"
                            }
                        ]
                    }}

                    options={{
                        elements: {
                            point: {
                                radius: 1
                            }
                        }
                    }}

                />
            </div>

        </div>
    )
}

export default Coinpage