import Cryptocard from "./Cryptocard"
import "../Styles/CryptoShowcase.css"
import { Crypto } from "../Context/Context"
import { useEffect, useState } from "react"
import axios from "axios"

const CryptoShowcase = () => {

    const [ market, setMarket ] = useState([])
    const [ search, setSearch ] = useState("")

    const { currency } = Crypto()

    useEffect( async () => {
        const { data } = await axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&order=market_cap_desc&per_page=100&page=1&sparkline=false`)
        setMarket(data)
    }, [ currency ] )

    const handleSearch = () => {
        return market.filter((val)=>{
            return(
                val.name.toLowerCase().includes(search) || 
                val.symbol.toLowerCase().includes(search)
            )
        })
    }

    return(
        <>
        <input onChange={ (e) => setSearch(e.target.value) } type="text" placeholder="Search Coin" />
        <div className="Cards">
            {
                handleSearch().map( (val, id) => {
                    return(
                        <Cryptocard key={id} {...val} />
                    )
                })
            }
        </div>
        </>
    )
}

export default CryptoShowcase