import { Link } from "react-router-dom"
import { Crypto } from "../Context/Context"
import "../Styles/Cryptocard.css"

const Cryptocard = (props) => {

    const { currency } = Crypto()

    return(
        <Link to={`/${props.id}`}>
            <div className="cryptocard">
            <img src={props.image} width="60px" height="60px" />
            <h2>{ props.name }</h2>
            <h4>{ props.symbol.toUpperCase() }</h4>

            {
                currency=="usd"? (
                    <h4>$ { props.current_price }</h4>
                ) : (
                    <h4> { props.current_price }</h4>
                )
            }
            

            {
                props.market_cap_change_percentage_24h > 0 ? (
                    <h5 style={{ color:"green" }}>
                        <i className="fa-solid fa-caret-up"></i>
                        { props.market_cap_change_percentage_24h } %
                    </h5>
                ):(
                    <h5 style={{ color: "tomato" }}>
                        <i className="fa-solid fa-caret-down"></i>
                        { props.market_cap_change_percentage_24h * -1 } %
                    </h5>   
                )
            }

        </div>
    </Link>
    )
}

export default Cryptocard